$primary: #FFADA9;
$primaryLight: #ffdedc;
$primaryDark:#ff7c76;

$secondary:#ffa9d0;
$secondaryLight: #ffcbe2;

$accent:#a9ffad;
$accentDark:#66e65c;


$textColor:#fff;

$fontsize: 16px;
$textcolor: rgba(0,0,0,.7);

$background: #fbfbfb;

$paddingSmall: 40px;
$paddingLarge: 60px;
$sidebarSmall: 8px;

$borderRadius:20px;

html,body {
  width:100%;
  height:100%;
}

body {
  font-size:$fontsize;
  font-family:'AdobeCaslonW01-Regular';
  color:$textcolor;
  background: $background;
}

p { text-rendering: optimizeLegibility; margin-bottom:15px; }
a { transition: all 0.270s ease-out 0s; }
a:hover,a:focus { text-decoration:none; }

.btn,button {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

a.btn:hover,
button:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.activeNav {
  &:after {
    background-color: #fff;
    width:100% !important;
    height:2px;
  }
}
ul,li,ol {
  list-style: none;
  list-style-image: none;
  padding:0;
}

.content {
  height: 100%;
  position: relative;
}